import React from "react";
import { Logger } from "../Models/Logger";

type SocketContextType = {
  socket: WebSocket;
}

export const SocketContext = React.createContext<SocketContextType>(undefined!);

const anyWindow = window as any;

anyWindow.reconnectionCount = 0;

export const connection = (onMessage: (event: any) => void, logger?: Logger): WebSocket | null => {
  try {
    const socket = new WebSocket(process.env.REACT_APP_SOCKET_URL!);

    socket.onmessage = onMessage;

    socket.onclose = (event) => {
      console.log('Socket closed by event:', event);

      console.log('Trying to reconnect...');
      setTimeout(() => {
        if (anyWindow.reconnectionCount >= 4) {
          console.error('Too many reconnection attempts');
          if (logger) {
            logger('There are too many reconnection attempts. You are disconnected', "error");
          }
          return;
        }
        anyWindow.reconnectionCount++;
        console.log("!!!!!: ", anyWindow.reconnectionCount);
        clearTimeout((window as any).clearReconnectionCounterTimeOut);

        try {
          const newConnection = connection(onMessage, logger);

          (window as any).clearReconnectionCounterTimeOut = setTimeout(() => {
            anyWindow.reconnectionCount = 0
          }, 10000);
          console.log('Successfully reconnected: ', newConnection);
        } catch (e) {
          console.error("Error of reconnection: ", e);
          if (logger) {
            logger('You were disconnected from the chat', 'error');
          }
        }
      }, 1000);
    }

    console.log('Successfully websocket connection:', socket);

    return socket;
  } catch (e) {
    console.log('Something went wrong while websocket connection: ', e);
    if (logger) {
      logger('You were disconnected from the chat', 'error');
    }
    // TODO: implement global API Error
    return null;
  }
}