import React, { FunctionComponent, useContext } from 'react';
import { MessagesContext } from '../../contexts/messages';
import './MessageList.css';

type MessagesListProps = {};

const MessagesList: FunctionComponent<MessagesListProps> = (props) => {
  const { messages } = useContext(MessagesContext);

  const messagesJSX = messages.map(message => {
    if ('type' in message) {
      return (
        <li className={`message--system message--system--${message.type}`} key={message.id} >
          { message.msg }
        </li>
      );
    } else {
      const date = new Date(message.date);

      return (
        <li className={`message ${message?.owner ? 'my' : '' }`} key={message.id} >
          <span className={"message__time"} >{date.getHours()}:{date.getMinutes()}</span>
          { message.text }
        </li>
      );
    }
  })

  return <ul className="messages_list">{
    messagesJSX
  }</ul>;
};

export default MessagesList;
