import { useContext, useEffect, useState } from 'react';

import { connection, SocketContext } from '../contexts/connection';
import { Logger } from "../Models/Logger";
// TODO: Moved request typing to backend
type RequestsTypes = 'message';

type Handlers = {
  [key in RequestsTypes]?: (data: any) => void;
}

type EventType = {
  type: RequestsTypes,
  data: any
}

const useConnection = (handlers: Handlers, log?: Logger) => {
  const [socket, setSocket] = useState<WebSocket | null>(null);

  const handleSocket = (event: EventType) => {
    if (event.type in handlers) {
      handlers[event.type]!(JSON.parse(event.data));
    } else {
      console.error(`Unhandled socket event type {${event.type}}:`, event);
    }
  }

  useEffect(() => {
    const connectionResult = connection(handleSocket, log)

    setSocket(connectionResult);

    if (log && connectionResult) {
      log('Successful connection to the chat. You can chat');
    }
  }, []);

  return socket;
};

export default useConnection;