import { useContext } from 'react';

import { SocketContext } from '../contexts/connection';
import { sendMessage as sendMessageRequest } from '../api/reuests';

const useSocketRequests = () => {
  const { socket } = useContext(SocketContext);

  const sendMessage = (message: string) => {
    sendMessageRequest(socket, { message });
  }

  return {
    sendMessage
  }
}

export default useSocketRequests;