import React, { FunctionComponent, useEffect, useState } from 'react';
import './UserList.css';

type UserListProps = {

}

// TODO: remove mocks
const mockedData = [
  {
    id: 'someid',
    userName: 'Username'
  },
  {
    id: 'someid23',
    userName: 'Vladimir',
    owner: true
  }
];

const UserList: FunctionComponent<UserListProps> = (props) => {
  const users = mockedData.sort(u1 => !u1.owner && 1 || -1);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const listener = (event: UIEvent) => {
      setWindowWidth((event.currentTarget! as Window).innerWidth);
    }
    window.addEventListener('resize', listener);

    return () => window.removeEventListener('resize', listener);
  }, []);

  return (
    <ul className={`main_sidebar__user_list ${windowWidth < 600 ? 'min' : ''}`}>
      { users.map(user => (
        <li key={user.id} className={`user_list__user ${user.owner ? 'my' : ''}`} >
          <div className="user__avatar"><span>{user.userName[0].toUpperCase()}</span></div>
          <span className="user__name">{ user.userName }</span>
        </li>
      ))}
    </ul>
  );
}

export default UserList;
