import React, { FunctionComponent } from 'react';
import './Bg.css';
//@ts-ignore
import videoSrc from '../../assets/bg.mp4';
import imgBgSrc from '../../assets/bg.png';

type BgProps = {

}

const Bg: FunctionComponent<BgProps> = (props) => {
  return <>
    <img
      src={imgBgSrc}
      className="static_bg"
    />
    <video
      className="live_bg"
      src={videoSrc}
      autoPlay={true}
      loop={true}
      muted={true}
      width="100%"
      height="100%"
    />
  </>;
}

export default Bg;
