import React, { ChangeEvent, FunctionComponent, useState } from 'react';
import useSocketRequests from "../../hooks/useSocketRequests";
import './MessageEditor.css';

type MessageEditorProps = {

}

const MessageEditor: FunctionComponent<MessageEditorProps> = (props) => {
  const [messageText, setMessageText] = useState('');
  const { sendMessage } = useSocketRequests();

  const validation = () => {
    if (messageText.length <= 0) {
      return false;
    }

    return true;
  }

  const handleChange = (e: ChangeEvent<HTMLTextAreaElement>) => {
    setMessageText(e.target.value);
  }

  const handleSend = () => {
    if (validation()) {
      setMessageText('');
      sendMessage(messageText);
    }
  }

  const handleKey = (event: any) => {
    if (event.code === 'Enter') {
      handleSend();
      event.preventDefault();
    }
  }

  return <div className={'message_editor_container'}>
    <textarea
      onKeyDown={handleKey}
      value={messageText}
      onChange={handleChange}
      name="msg_editor"
      id="msg_editor_textarea"
      cols={5}
      rows={10}
    />
    <button onClick={handleSend} className="message_editor__send_button" />
  </div>;
}

export default MessageEditor;
