import React, { FunctionComponent, useState } from 'react';

import { SocketContext } from '../contexts/connection';
import { MessagesContext } from '../contexts/messages';
import useConnection from '../hooks/useConnection';
import { Message } from '../Models/Message';
import { Logger, SystemMessage } from "../Models/Logger";

const GlobalContext: FunctionComponent = ({ children }) => {
  const [messages, setMessages] = useState<(Message | SystemMessage)[]>([]);

  const onMessage = (data: Message) => {
    console.log('Received message:', data);
    setMessages((prevState) => [...prevState, data]);
  };

  const pushMessage = (message: Message | SystemMessage) => {
    setMessages((prevState) => [...prevState, message]);
  }

  const log: Logger = (message: string, type= 'info') => {
    pushMessage({
      type,
      msg: message,
      id: new Date().getTime().toString()
    });
  }

  const socket = useConnection({
    message: onMessage,
  }, log);

  if (!socket) {
    return <>Loading...</>;
  }

  return (
    <SocketContext.Provider value={{ socket: socket }}>
      <MessagesContext.Provider value={{ messages, pushMessage }}>
        {children}
      </MessagesContext.Provider>
    </SocketContext.Provider>
  );
};

export default GlobalContext;