import { Message } from '@webchat_test/messages';

type SocketRequest<Data> = (socket: WebSocket, data: Data) => unknown;

export const sendMessage: SocketRequest<{ message: Message['text'] }> = (
  socket: WebSocket,
  { message }
) => {
  const msg = {
    action: 'sendmessage',
    text: message,
  };

  socket.send(JSON.stringify(msg));
};
